/*Обнуление*/
*{
	padding: 0;
	margin: 0;
	border: 0;
}

*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:focus,:active{
    outline: none;
}

a:focus,a:active{
    outline: none;
    color:inherit;
}

nav,footer,header,aside{
    display: block;
}

html,body{
	width: 100%;
    min-width: 360px;
    padding: 0px;
    margin: 0px;
	font-size: 100%;
    font-size: 20px;
	line-height: 1;
    font-family: Raleway;
    color: #2d2d2d;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
    background: linear-gradient(180deg, rgba(216, 224, 247, 0.5) 0%, rgba(224, 171, 233, 0.42) 100%);
}
input,button,textarea{
    font-family:inherit;
}

input::-ms-clear{
    display: none;
}

button{
    cursor: pointer;
}

button::-moz-focus-inner {
    padding:0;
    border:0;
}

a, a:visited{
    text-decoration: none;
    color:inherit;
}

a:hover{
    text-decoration: none;
}

ul li{
    list-style: none;
}

img{
    vertical-align: top;
}

h1,h2,h3,h4,h5,h6{
    font-size:inherit;
    font-weight: 400;
}

